import { Card } from "@/components/ui/card"
import { TrendingUp, Lock, Users } from 'lucide-react'

export function StakingStats() {
  const stats = [
    {
      icon: TrendingUp,
      label: "Total Value Locked",
      value: "$10.5M"
    },
    {
      icon: Users,
      label: "Active Stakers",
      value: "10,234"
    },
    {
      icon: Lock,
      label: "Average Lock Period",
      value: "67 Days"
    }
  ]

  return (
    <div className="space-y-4">
      {stats.map((stat, idx) => {
        const Icon = stat.icon
        return (
          <Card key={idx} className="p-6 bg-card/50 backdrop-blur-sm border border-primary/10">
            <div className="flex items-start gap-3">
              <div className="p-2 bg-primary/10 rounded-lg">
                <Icon className="w-5 h-5 text-primary" />
              </div>
              <div>
                <p className="text-sm text-muted-foreground mb-1">{stat.label}</p>
                <p className="text-2xl font-bold">{stat.value}</p>
              </div>
            </div>
          </Card>
        )
      })}

      {/* Info box */}
      <Card className="p-6 bg-secondary/5 border border-secondary/20 space-y-3">
        <h3 className="font-semibold">How it Works</h3>
        <ul className="text-sm text-muted-foreground space-y-2">
          <li className="flex gap-2">
            <span className="text-primary font-bold">1.</span>
            <span>Connect your Algorand wallet</span>
          </li>
          <li className="flex gap-2">
            <span className="text-primary font-bold">2.</span>
            <span>Enter stake amount and lock period</span>
          </li>
          <li className="flex gap-2">
            <span className="text-primary font-bold">3.</span>
            <span>Receive NFT stake token</span>
          </li>
          <li className="flex gap-2">
            <span className="text-primary font-bold">4.</span>
            <span>Claim rewards after lock ends</span>
          </li>
        </ul>
      </Card>
    </div>
  )
}
